.body{
  height: 100vh;
  width: 100%;
  position: relative;
  background-color: #E8620C;
  .feedbacksBody{
    margin-top: 300px;
  }
  .circles{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 7px;
    align-items: center;
    flex-wrap: nowrap;
  }
  .slide{
    padding: 15px;
    background-color: #179c54;
    border-radius: 20px;
    width: 280px;
    height: 190px;
    p{
      color: #fffcd2;
    }
    .text{
      text-align: justify;
      span{
        margin: 0;
        font-weight: 700;
      }
    }
    .name{
      color: #fffcd2;
      font-weight: 600;
    }
  }
}